                  、TWD97Lat(TWD97緯度)、TWD97TM2X(TWD97二度分帶X)、TWD97TM2Y(TWD97二度分帶Y)、SampleDate(採樣日期)、SampleLayer(採樣水層)"
                  、SampleDepth(採樣深度)、ItemName(測項名稱)、ItemEngName(測項英文名稱)、ItemEngAbbreviation(測項英文簡稱)、ItemValue(監測值)、ItemUnit(測項單位)
