<script>
import moment from "moment";
import axios from "axios";
export default {
  data() {
    return {
      AllKinds: [
        "降雨",
        "水庫",
        "用水",
        "汙水處理廠",
        "環境水體",
        "地下水",
        // "淨水廠",
        "水法規",
      ],
      searchKind: "降雨",
      hisDam: "",
      CTSIdam: "",
      AllCTSIDam: "",
      nowYear: "",
      pollutionRiver: "",
      hisPollutionRiver: "",
      hisRPIPollutionRiver: "",
      hisDamYear: [],
      AllhisDamYear: [],
      dirtyWaterYearArray: ["104", "105", "106", "107", "108"],
      AllgroundWaterDis: "",
      AllgroundWaterYear: "",
      pollutionYearArray: [
        "100",
        "101",
        "102",
        "103",
        "104",
        "105",
        "106",
        "107",
        "108",
      ],
      groundWaterYearArray: [
        "2014",
        "2015",
        "2016",
        "2017",
        "2018",
        "2019",
        "2020",
      ],
      AllgroundWaterYearArray: [
        "2014",
        "2015",
        "2016",
        "2017",
        "2018",
        "2019",
        "2020",
      ],
      AllgroundWaterDisArray: [
        "鳳山區",
        "梓官區",
        "大社區",
        "岡山區",
        "永安區",
        "路竹區",
        "阿蓮區",
        "鳥松區",
        "仁武區",
        "橋頭區",
        "湖內區",
        "美濃區",
        "旗山區",
        "大寮區",
        "林園區",
        "楠梓區",
        "苓雅區",
        "小港區",
        "左營區",
        "旗津區",
        "鼓山區",
        "新興區",
        "大樹區",
        "茄萣區",
        "六龜區",
        "杉林區",
      ],
      groundWaterDisArray: [],
      groundWaterStationArray: [],
      groundWaterDis: "",
      // groundWaterStation: "",
      groundWaterYear: "",
      pollutionYear: "",
      dirtyWaterYear: "",
      DamYear: "",
      AllDamYear: "",
    };
  },
  mounted() {
    this.nowYear = moment(new Date()).format("YYYY");
    this.getGroundWaterDisArray();
  },
  methods: {
    getGroundWaterDisArray() {
      axios
        .post(`${this.allURL}groundWater/stationName`)
        .then((res) => {
          console.log(res.data);
          res.data.siteData[0].forEach((item) => {
            this.groundWaterDisArray.push(item.name);
          });
          for (let i = 1; i < res.data.siteData.length; i++) {
            let stationDetail = {
              name: "",
              dis: "",
            };
            stationDetail.name = res.data.siteData[i].SiteName;
            stationDetail.dis = res.data.siteData[i].dis;
            this.groundWaterStationArray.push(stationDetail);
          }
          console.log(this.groundWaterStationArray);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    downloadData(url, data, name, index) {
      if (this.searchKind === "水庫") {
        switch (index) {
          case 1:
            if (this.damName === "" || this.DamYear === "") {
              this.message();
              return;
            }
            break;
          case 2:
            if (this.hisDam === "" || this.AllDamYear === "") {
              this.message();
              return;
            }
            break;
          case 3:
            if (this.CTSIdam === "") {
              this.message();
              return;
            }
            break;
        }
      }
      if (this.searchKind === "汙水處理廠") {
        if (this.dirtyWaterYear === "") {
          this.message();
          return;
        }
      }
      if (this.searchKind === "環境水體") {
        switch (index) {
          case 1:
            if (this.pollutionYear === "" || this.pollutionRiver === "") {
              this.message();
              return;
            }
            break;
          case 2:
            if (this.hisPollutionRiver === "") {
              this.message();
              return;
            }
            break;
          case 3:
            if (this.hisRPIPollutionRiver === "") {
              this.message();
              return;
            }
            break;
        }
      }
      if (this.searchKind === "地下水") {
        switch (index) {
          case 1:
            console.log(this.AllgroundWaterDis);
            console.log(this.AllgroundWaterYear);
            if (
              this.AllgroundWaterDis === "" ||
              this.AllgroundWaterYear === ""
            ) {
              this.message();
              return;
            }
            break;
          case 2:
            if (this.groundWaterDis === "" || this.groundWaterYear === "") {
              this.message();
              return;
            }
            break;
        }
      }
      axios
        .post(url, data, { responseType: "blob" })
        .then((res) => {
          this.download(res, `${name}.json`);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    message() {
      this.$message({
        message: "請確認欲下載條件皆填寫完成!!",
        type: "warning",
      });
    },
    download(res, name) {
      if (!res) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([res.data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
  },
  watch: {
    AllCTSIDam() {
      this.hisDamYear = [];
      switch (this.AllCTSIDam) {
        case "阿公店水庫":
          for (let i = 2007; i <= this.nowYear; i++) {
            this.hisDamYear.push(i);
          }
          break;
        case "澄清湖水庫":
          for (let i = 2005; i <= this.nowYear; i++) {
            this.hisDamYear.push(i);
          }
          break;
        case "鳳山水庫":
          for (let i = 2005; i <= this.nowYear; i++) {
            this.hisDamYear.push(i);
          }
          break;
      }
    },
    hisDam() {
      this.AllhisDamYear = [];
      switch (this.hisDam) {
        case "阿公店水庫":
          for (let i = 2007; i <= this.nowYear; i++) {
            this.AllhisDamYear.push(i);
          }
          break;
        case "澄清湖水庫":
          for (let i = 2005; i <= this.nowYear; i++) {
            this.AllhisDamYear.push(i);
          }
          break;
        case "鳳山水庫":
          for (let i = 2005; i <= this.nowYear; i++) {
            this.AllhisDamYear.push(i);
          }
          break;
      }
    },
  },
};
</script>
